
.commentInput div{
    margin: 0 0 30px 0;
    display: flex;
}
.commentInput{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
}
.commentInput input{
    width: 240px;
    height: 40px;
    margin: 0 0px 0 20px;
    background: transparent;
    border: 2px solid wheat;
    border-radius: 9px;
    color: wheat;
    padding: 10px 20px;
    font-size: 23px;
}
.commentInput button{
    font-weight: 800;
    font-size: 25px;
    transition: .4s;
    background: transparent;
    border: '2px solid aqua';
    border-radius: 10px;
    color: aqua;
    width: 280px;
    height: 60px;
}
.commentInput button:hover{
    border: none;
    cursor: pointer;
    background: aqua;
    color:rgb(5, 10, 36);
}